import React, { useEffect, useState } from "react";
import LogoUERJ from "../resources/UERJ_Logo.svg";

import styled from "styled-components";

const View = styled.div`
  display: flex;
  flex-direction: row;
  height: 9vh;
  min-height: 70px;
  justify-content: space-between;
  background-color: ${(props) => props.theme.secondaryColor};
`;

const Text = styled.p`
  font-size: 16pt;
  font-weight: 600;
  text-align: right;
  padding-right: 2%;
  color: ${(props) => props.theme.white};
`;

const Row = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  width: 80vw;
`;

const Logo = styled.img`
  width: 30pt;
  padding-right: 2%;
`;

const HeaderComponent = ({ boardName }) => {
  return (
    <View>
      <Row>
        <Text>{boardName}</Text>
        <Logo src={LogoUERJ} alt="React Logo" />
      </Row>
    </View>
  );
};

export default HeaderComponent;
