import React, { useEffect, useState, useRef } from "react";
import AceEditor from "react-ace";
import socket from "../services/socket";

import { store } from "../store";
import { changeCode } from "../store/actions";

import "ace-builds/src-noconflict/mode-python";
import "ace-builds/src-noconflict/mode-javascript";
import "ace-builds/src-noconflict/mode-r";

import "ace-builds/src-noconflict/theme-xcode";
import "ace-builds/src-noconflict/theme-dracula";

import "ace-builds/src-noconflict/ext-language_tools";
import "ace-builds/webpack-resolver";

const Codeboard = ({ boardName, boardStyle, boardLanguage }) => {
  const [code, setCode] = useState("print('Hello World!')");

  const codeboardRef = useRef(null);
  const { user, selectedUser } = store.getState();

  useEffect(() => {
    setCode(store.getState().user[boardName]);

    function undo() {
      console.log("undo trigger");
    }

    function redo() {
      console.log("redo trigger");
    }
    const { editor } = codeboardRef.current;
    const { commands } = editor;
    commands.commands.undo.exec = undo;
    commands.commandKeyBinding["ctrl-z"].exec = undo;
    commands.commands.redo.exec = redo;
    commands.commandKeyBinding["ctrl-y"].exec = redo;
  }, [boardName, boardStyle, boardLanguage]);

  useEffect(
    () =>
      store.subscribe(() => {
        // console.log("rendered");
        setCode(store.getState().selectedUser.code);
        codeboardRef.current.editor.focus();
      }),
    []
  );

  function onChangeText(text) {
    const { id } = store.getState().user;
    store.dispatch(changeCode(boardName, text, id));
    const dataObject = { id, code: text };
    socket.emit(boardName, dataObject);
  }

  function onLoad(event) {
    event.focus();
  }

  return (
    <AceEditor
      focus
      ref={codeboardRef}
      readOnly={user.id !== selectedUser.id}
      mode={boardLanguage}
      theme={boardStyle.theme}
      fontSize={boardStyle.fontSize}
      tabSize={boardStyle.tabSize}
      enableBasicAutocompletion={boardStyle.autoComplete}
      enableLiveAutocompletion={boardStyle.autoComplete}
      style={{ width: "80vw", height: "91vh" }}
      name="CodeEditor"
      onLoad={onLoad}
      onChange={onChangeText}
      showPrintMargin={false}
      highlightActiveLine={true}
      showGutter={true}
      value={user.id === selectedUser.id ? code : selectedUser.code}
    />
  );
};

export default Codeboard;
