import React, { useEffect, useState } from "react";

import { decodeID } from "../utils/parser";
import { store } from "../store";

import { changeSelectedUser } from "../store/actions";

import styled from "styled-components";
import { FaFileCode } from "react-icons/fa";

const View = styled.div`
  display: flex;
  flex-direction: column;
  width: 20vw;
  background-color: ${(props) => props.theme.primaryColor};
`;

const Header = styled.p`
  font-size: 16pt;
  font-weight: 600;
  font-family: "Fira Code VF", monospace;
  color: ${(props) => props.theme.white};
  align-self: center;
`;

const HeaderView = styled.div`
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 9vh;
  min-height: 70px;
  justify-content: center;
  background-color: ${(props) => props.theme.secondaryColor};
`;

const Text = styled.p`
  font-size: 16pt;
  font-weight: 600;
  color: ${(props) => props.theme.white};
  padding-left: 5;
`;

const ItemDiv = styled.div`
  display: flex;
  flex-direction: row;
  padding-left: 5%;
  padding-right: 5%;
  align-items: center;
  background-color: ${(props) =>
    props.isSelected ? props.theme.secondaryColor : props.theme.primaryColor};
`;

const SidePanel = ({ data, dataUsers }) => {
  const [users, setUsers] = useState([]);
  const [selectedUserID, setSelectedUserID] = useState("");

  useEffect(() => {
    const decodedUsers = [];
    const userlist = Object.keys(data);
    userlist.map((encodedUser) => {
      if (!!encodedUser) {
        const { username, password, id } = decodeID(encodedUser);
        const userData = {
          username,
          password,
          id,
          code: data[encodedUser].code,
        };
        return decodedUsers.push(userData);
      }
      return null;
    });
    setUsers(decodedUsers);

    window.addEventListener("resize", (event) => {
      //   console.log("resize", event.currentTarget.innerWidth);
    });
  }, [data]);

  useEffect(() => {
    setSelectedUserID(store.getState().user.id);
    store.subscribe(() => {
      console.log("rendered sidepanel");
      setSelectedUserID(store.getState().selectedUser.id);
    });
  }, []);

  function changeUser(user) {
    store.dispatch(changeSelectedUser(user.id, user.code));
  }

  return (
    <View>
      <HeaderView>
        <Header>{"<CodeBoard/>"}</Header>
      </HeaderView>
      {users.map((user, key) => {
        return (
          <ItemDiv
            isSelected={selectedUserID === user.id}
            key={key}
            onClick={() => changeUser(user)}
          >
            <FaFileCode color={"#fff"} size={"22pt"} />
            <Text>{user.username}</Text>
          </ItemDiv>
        );
      })}
    </View>
  );
};

export default SidePanel;
