export function changeLanguage(language, boardName) {
  return {
    type: "CHANGE_LANGUAGE",
    boardName,
    language,
  };
}

export function changeSelectedUser(id, code) {
  return {
    type: "CHANGE_SELECTED_USER",
    id,
    code,
  };
}

export function registerUser(id, code) {
  return {
    type: "REGISTER_USER",
    id,
  };
}

export function changeCode(board, code, id) {
  return {
    type: "CHANGE_CODE",
    board,
    code,
    id,
  };
}
