import { createStore } from "redux";
import { persistStore, persistReducer } from "redux-persist";
import storage from "redux-persist/lib/storage";

const INITIAL_STATE = {
  user: {
    id: undefined,
  },
  selectedUser: {
    id: undefined,
    code: undefined,
  },
  language: { default: "python" },
  styles: {
    fontSize: 16,
    theme: "xcode",
    tabSize: 2,
    autoComplete: true,
  },
};

function reducer(state = INITIAL_STATE, action) {
  switch (action.type) {
    case "CHANGE_LANGUAGE":
      return {
        ...state,
        language: {
          ...state.language,
          [action.boardName]: action.language,
        },
      };
    case "CHANGE_CODE":
      return {
        ...state,
        user: { ...state.user, [action.board]: action.code },
        selectedUser: { id: action.id, code: action.code },
      };
    case "CHANGE_SELECTED_USER":
      return {
        ...state,
        selectedUser: { id: action.id, code: action.code },
      };
    case "REGISTER_USER":
      return {
        ...state,
        user: { id: action.id },
        selectedUser: { id: action.id },
      };
    default:
      return state;
  }
}

const persistConfig = {
  key: "root",
  storage,
};

const persistedReducer = persistReducer(persistConfig, reducer);

export const store = createStore(persistedReducer);
export const persistor = persistStore(store);
