import React, { useEffect, useState } from "react";

import Codeboard from "../components/codeboard";
import SidePanel from "../components/sidePanel";
import HeaderComponent from "../components/header";
import RegisterModal from "../components/register";

import socket from "../services/socket";

import { store } from "../store";
import { changeSelectedUser, changeLanguage } from "../store/actions";

import { View, Column } from "./styles";

const boardName = window.location.pathname
  .toLowerCase()
  .replace("/codeboard/", "");

console.log("window", window.location.pathname);
console.log("boardName", boardName);

const Home = () => {
  const [data, setData] = useState(null);
  const [isVisible, setIsVisible] = useState(true);
  const [styles, setStyles] = useState({});
  const [language, setLanguage] = useState("javascript");

  function loadStylesAndLanguage() {
    const { language } = store.getState();
    setStyles(store.getState().styles);
    if (!language[boardName]) {
      store.dispatch(changeLanguage(language.default, boardName));
    }
    setLanguage(language[boardName]);
  }

  function connectToBoardSocket() {
    setIsVisible(false);

    store.dispatch(
      changeSelectedUser(
        store.getState().user.id,
        store.getState().user[boardName]
      )
    );

    socket.emit("codeboard", boardName);

    const dataObject = {
      id: store.getState().user.id,
      code: store.getState().user[boardName] || "print('Hello World!')",
    };
    socket.emit(boardName, dataObject);

    setData((c) => ({ ...c, [dataObject.id]: dataObject }));
    socket.on(boardName, (newData) => {
      setData((c) => ({ ...c, ...newData }));
      const selectedUserData =
        newData[
          Object.keys(newData).find(
            (id) => id === store.getState().selectedUser.id
          )
        ];
      store.dispatch(
        changeSelectedUser(selectedUserData?.id, selectedUserData?.code)
      );
    });
  }

  useEffect(() => {
    loadStylesAndLanguage();
    if (!!store.getState().user.id) {
      connectToBoardSocket();
    }
  }, [isVisible]);

  useEffect(() => store.subscribe(loadStylesAndLanguage), []);

  return (
    <View>
      <RegisterModal
        isVisible={isVisible}
        closeModal={() => setIsVisible(false)}
      />
      <SidePanel
        data={data ? data : []}
        whoIsWriting={data ? Object.keys(data) : []}
      />
      <Column>
        <HeaderComponent boardName={boardName} />
        {!isVisible && (
          <Codeboard
            boardName={boardName}
            boardStyle={styles}
            boardLanguage={language}
          />
        )}
      </Column>
    </View>
  );
};

export default Home;
