import React, { useEffect, useState } from "react";
import Modal from "react-modal";

import { encodeID } from "../utils/parser";
import { store } from "../store";

import { registerUser } from "../store/actions";

import styled from "styled-components";

const View = styled.div`
  padding-left: 5%;
  padding-right: 5%;
  padding-top: 10%;
  padding-bottom: 10%;
  display: flex;
  flex-direction: column;
  width: 50vw;
  background-color: #0f0;
  align-self: center;
`;
const Input = styled.input``;
const Button = styled.button`
  margin-top: 10%;
`;

const modalStyle = {
  overlay: {
    position: "fixed",
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    backgroundColor: "rgba(255, 255, 255, 0.5)",
  },
  content: {
    display: "flex",
    position: "absolute",
    top: "0px",
    left: "0px",
    right: "0px",
    bottom: "0px",
    border: "0px",
    background: "transparent",
    overflow: "auto",
    WebkitOverflowScrolling: "touch",
    borderRadius: "0px",
    outline: "none",
    padding: "0px",
    justifyContent: "center",
  },
};

const RegisterModal = ({ isVisible, closeModal }) => {
  const [isOpen, setIsOpen] = useState(false);
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");

  useEffect(() => {
    setIsOpen(isVisible);
  }, [isVisible]);

  function handleRegister() {
    const id = encodeID(username, password);
    store.dispatch(registerUser(id));
    return closeModal();
  }

  return (
    <Modal isOpen={isOpen} contentLabel="Example Modal" style={modalStyle}>
      <View>
        <h1>Usu√°rio</h1>
        <Input onChange={(e) => setUsername(e.target.value)} />
        <h1>Senha</h1>
        <Input onChange={(e) => setPassword(e.target.value)} />
        <Button onClick={handleRegister}>Registrar</Button>
      </View>
    </Modal>
  );
};

export default RegisterModal;
